import React, { useCallback, useState } from "react";
import s from "./LeftSideDashboard.module.scss";
import { Avatar, Button, IconButton } from "@material-ui/core";
import { SearchSharp, Settings } from "@material-ui/icons";
import ChatIcon from "../../../assets/icons/chat.svg";

const chatList = ["New Chat", "Paywall for fintech mobile app, that offers"];

interface IProps {
  name: string;
  onClick: any;
  selected: boolean;
}

const ChatNameItem: React.FC<IProps> = ({ name, onClick, selected }) => {
  return (
    <div
      className={s.chatName}
      onClick={onClick}
      style={{ backgroundColor: selected ? "#CFE6F7" : "white" }}
    >
      <Avatar src={ChatIcon} className={s.icon}/>
      <div className={s.name}>{name}</div>
    </div>
  );
};

const LeftSideDashboard = () => {
  const [selectedChat, setSelectedChat] = useState("New Chat");

  const handleSelectChat = useCallback((name) => {
    setSelectedChat(name);
  }, []);

  return (
    <div className={s.dashboardContainer}>
      <div className={s.controlPanel}>
        <Button variant="contained" color="primary">
          + New Chat
        </Button>
        <IconButton className={s.search}>
          <SearchSharp />
        </IconButton>
      </div>
      <div className={s.chatNameList}>
        {chatList.map((c) => (
          <ChatNameItem
            key={c}
            name={c}
            onClick={() => handleSelectChat(c)}
            selected={selectedChat === c}
          />
        ))}
      </div>
      <div className={s.settings}>
        <IconButton className={s.settingsIcon}>
          <Settings />
          <div className={s.iconName}>Settings</div>
        </IconButton>
      </div>
    </div>
  );
};

export default LeftSideDashboard;
