import {
  GetQueryListResponse,
  ResetQueryArgs,
  ResetResponse,
  SendQueryArgs,
  SendQueryResponse,
} from "./types";
import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";

export const MAIN_API_REDUCER_KEY = "main";

export const mainAPI = createApi({
  reducerPath: MAIN_API_REDUCER_KEY,
  baseQuery: fetchBaseQuery({ baseUrl: "/" }),
  endpoints: (builder) => ({
    getQueryList: builder.query<GetQueryListResponse, null>({
      query: () => {
        return {
          url: "query",
          method: "GET",
        };
      },
    }),
    sendQuery: builder.query<SendQueryResponse, SendQueryArgs>({
      query: (body) => {
        return {
          url: "query",
          method: "POST",
          body,
        };
      },
    }),
    reset: builder.query<ResetResponse, ResetQueryArgs>({
      query: (body) => {
        return {
          url: "reset",
          method: "POST",
        };
      },
    }),
  }),
});
