import {
  Box,
  Button,
  Grid,
  IconButton,
  InputBase,
  LinearProgress,
  Typography,
} from "@material-ui/core";
import React, { useCallback, useEffect, useRef, useState } from "react";
import Message from "../../shared/components/Chat/Message";
import { mainAPI } from "../../api/main/api";
import s from "./Messenger.module.scss";
import { SearchSharp } from "@material-ui/icons";
import { useSelector } from "react-redux";
import { MessageBody } from "../../api/main/types";

const Messenger: React.FC<any> = () => {
  const lastMessageRef = useRef<HTMLDivElement>(null);
  const [message, setMessage] = useState<string>("");

  const queryList = useSelector((state: any) => state.mainSlice.queryList);

  const [getQueryList, { isFetching: getQueriesIsFetching }] =
    mainAPI.useLazyGetQueryListQuery();
  const [resetQueries, { isFetching: resetQueriesIsFetching }] =
    mainAPI.useLazyResetQuery();

  const [sendQuery, { isFetching, isSuccess }] =
    mainAPI.useLazySendQueryQuery();

  useEffect(() => {
    getQueryList(null);
  }, [getQueryList]);

  useEffect(() => {
    if (queryList && lastMessageRef.current) {
      lastMessageRef.current.scrollIntoView();
    }
  }, [queryList]);

  const handleChangeMessage = useCallback((e) => {
    setMessage(e.target.value);
  }, []);

  const handleSendQuery = useCallback(() => {
    if (message) {
      sendQuery({ query: message });
    }
  }, [message, sendQuery]);

  const handlePressEnter = useCallback(
    (e) => {
      if (e.code === "Enter" && e.target.value) {
        sendQuery({ query: e.target.value });
      }
    },
    [sendQuery]
  );

  const handleReset = useCallback(() => {
    resetQueries({});
  }, [resetQueries]);

  useEffect(() => {
    if (!isFetching && isSuccess) {
      setMessage("");
      getQueryList(null);
    }
  }, [getQueryList, isFetching, isSuccess]);

  return (
    <Box
      flex="1 1 auto"
      height="100%"
      alignItems="center"
      justifyContent="center"
      display="flex"
      className={s.messengerContainer}
    >
      <Grid
        container
        direction="column"
        justify="center"
        alignItems="flex-start"
        xs={12}
        style={{ position: "relative", height: "100vh", marginTop: "auto" }}
      >
        <Grid
          item
          xs={12}
          style={{
            height: "82px",
            position: "absolute",
            backgroundColor: "white",
            width: "100%",
            top: "0",
            zIndex: 2,
            padding: 10,
            paddingTop: 15,
            paddingLeft: 30,
            paddingRight: 30,
            borderBottom: "1px solid #CDD2D6",
            display: "flex",
          }}
        >
          <Typography style={{ fontSize: 33 }}>New Chat</Typography>
          <div className={s.titleButtons}>
            <IconButton className={s.search}>
              <SearchSharp />
            </IconButton>
            <Button onClick={handleReset}>RESET</Button>
          </div>
        </Grid>
        <div
          style={{
            padding: "100px 20px 80px",
            overflowY: "auto",
            marginTop: "auto",
          }}
        >
          {queryList
            ? queryList.messages.map((q: MessageBody, idx: any) => (
                <Message key={`q-${idx}`} messageBody={q} />
              ))
            : null}
          <div ref={lastMessageRef} />
        </div>
        <Grid
          item
          xs={12}
          style={{
            position: "absolute",
            backgroundColor: "white",
            width: "100%",
            bottom: "0",
            zIndex: 2,
            padding: 10,
            height: 70,
            paddingLeft: 50,
            borderTop: "1px solid #CDD2D6",
          }}
        >
          <Grid container xs={12} style={{ height: "100%" }}>
            <Grid
              item
              xs={12}
              style={{
                position: "absolute",
                top: 0,
                zIndex: 3,
                left: 0,
                height: 10,
                width: "100%",
              }}
            >
              {isFetching || getQueriesIsFetching || resetQueriesIsFetching ? (
                <LinearProgress />
              ) : null}
            </Grid>
            <Grid item xs={10} style={{ display: "flex" }}>
              <InputBase
                fullWidth
                placeholder="Start type here"
                onChange={handleChangeMessage}
                value={message}
                onKeyPress={handlePressEnter}
              />
            </Grid>
            <Grid item xs={2} style={{ display: "flex" }}>
              <Button
                variant="text"
                style={{ color: "#3597DE" }}
                disabled={isFetching}
                onClick={handleSendQuery}
              >
                Send
              </Button>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </Box>
  );
};

export default Messenger;
