import React from "react";
import s from "./RightSideDashboard.module.scss";
import GmailIcon from "../../../assets/icons/gmail.svg";
import PropBoxIcon from "../../../assets/icons/dropbox.svg";
import TaskIcon from "../../../assets/icons/task.svg";
import { Avatar, Switch } from "@material-ui/core";

interface IProps {
  name: string;
  icon: any;
  checked: boolean;
}

const Source: React.FC<IProps> = ({ name, icon, checked }) => {
  return (
    <div className={s.source}>
      <Avatar src={icon} className={s.sourceIcon} />
      <div className={s.name}>{name}</div>
      <Switch
        className={s.switch}
        checked={checked}
        color="primary"
        size="medium"
      />
    </div>
  );
};

const sources = [
  { name: "Gmail", icon: GmailIcon, checked: true },
  { name: "Dropbox", icon: PropBoxIcon, checked: false },
];
const agents = [{ name: "My active tasks", icon: TaskIcon, checked: false }];

const RightSideDashboard = () => {
  return (
    <div className={s.dashboardContainer}>
      <div className={s.component}>
        <h2 className={s.componentTitle}>Chat Data Sources</h2>
        {sources.map((s) => {
          return (
            <Source
              key={s.name}
              name={s.name}
              icon={s.icon}
              checked={s.checked}
            />
          );
        })}
        <div className={s.addButtonLink}>+ Add data source</div>
      </div>
      <div className={s.component}>
        <h2 className={s.componentTitle}>Chat Agents</h2>
        {agents.map((s) => {
          return (
            <Source
              key={s.name}
              name={s.name}
              icon={s.icon}
              checked={s.checked}
            />
          );
        })}
        <div className={s.addButtonLink}>+ Add agent</div>
      </div>
    </div>
  );
};

export default RightSideDashboard;
