import { CssBaseline } from "@material-ui/core";
import React from "react";
import { Provider } from "react-redux";
import { BrowserRouter as Router, Route } from "react-router-dom";
import { PersistGate } from "redux-persist/integration/react";
import { QueryParamProvider } from "use-query-params";
import "./index.css";
import Auth from "./features/auth/Auth";
import UserMiddleware from "./features/auth/components/UserMiddleware/UserMiddleware";
import Dashboard from "./features/dashboard/Dashboard";
import FullscreenProgress from "./shared/components/FullscreenProgress/FullscreenProgress";
import { persistor, store } from "./shared/redux/store";
import MainDashboard from "./features/MainDashboard";
import { MuiThemeProvider, createMuiTheme } from "@material-ui/core/styles";
import { blue } from "@material-ui/core/colors";

const theme = createMuiTheme({
  palette: {
    primary: {
      main: blue[500],
    },
    secondary: {
      main: "#f44336",
    },
  },
});

const App = () => {
  return (
    <Provider store={store}>
      <MuiThemeProvider theme={theme}>
        <PersistGate loading={<FullscreenProgress />} persistor={persistor}>
          <Router>
            <QueryParamProvider ReactRouterRoute={Route}>
              <CssBaseline />
              <MainDashboard />
            </QueryParamProvider>
          </Router>
        </PersistGate>
      </MuiThemeProvider>
    </Provider>
  );
};

export default App;
