import { Avatar, Grid, Typography } from "@material-ui/core";
import React from "react";
import { MessageBody } from "../../../api/main/types";
import logo from "../../../assets/icons/robots.svg";

interface IProps {
    messageBody: MessageBody;
    key: string | number;
}

const Message: React.FC<IProps> = ({ messageBody, key }) => {
    const ai = messageBody.role === "myWorker";

    return (
        <Grid container wrap="nowrap" key={key} style={{ marginBottom: 12 }}>
            <Grid item style={{ marginTop: "auto" }}>
                <Avatar src={ai ? logo : ""} style={{ border: "1px solid #CDD2D6" }}>
                    {messageBody.role ? messageBody.role.charAt(0) : "U"}
                </Avatar>
            </Grid>
            <Grid item xs={10} style={{ paddingTop: 7, paddingLeft: 15 }}>
                <Typography noWrap={false} style={{ fontWeight: ai ? 400 : 600 }}>
                    <pre
                        style={{
                            whiteSpace: "pre-wrap",
                            fontFamily: "sans-serif",
                            margin: 0,
                        }}
                    >
                        {messageBody.content.trim()}
                    </pre>
                </Typography>
            </Grid>
        </Grid>
    );
};

export default Message;
